import React from "react";
import {
  Container,
  Button,
  Form,
  Row,
  Col,
  ProgressBar,
} from "react-bootstrap";
import { Link } from "react-router-dom";

import Alert from "./UI/Alert";

import classes from "./Content.module.css";

import sectoren from "./arrays/sectoren";
import leeftijden from "./arrays/leeftijden";

const Content = (props) => {
  const {
    stage,
    clicked,
    changed,
    answers,
    length,
    alertMessage,
    email,
    alertColor,
  } = props;

  let title = (
    <div>
      <h1>Vraag {stage}</h1>
    </div>
  );
  let content = null;
  let buttons = (
    <div>
      <Button onClick={clicked} variant="secondary" name="vorige">
        Vorige
      </Button>
      <Button onClick={clicked} variant="secondary" name="volgende">
        Volgende
      </Button>
    </div>
  );
  let radios = [];

  switch (stage) {
    case 0:
      title = <h1>Welkom</h1>;
      content = (
        <div>
          <p>Bedankt dat je de moeite neemt om mee te werken aan deze poll!</p>
          <p>
            Je kunt op elk moment op 'vorige' drukken om je antwoorden aan te
            passen. Je kunt de poll slechts één keer invullen.
          </p>
          <br />
        </div>
      );
      buttons = (
        <Button onClick={clicked} variant="secondary" name="start">
          Start
        </Button>
      );
      break;
    case 1:
      radios = [
        "100%, ik ben namelijk positief getest op het virus",
        "Groot. Ik ben niet getest maar heb wel symptomen gehad.",
        "Klein",
        "Ik weet het niet",
      ];
      content = (
        <div>
          <p>
            Hoe groot acht je de kans dat je het nieuwe Covid-19 virus hebt
            (gehad)?
          </p>

          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {radios.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 2:
      const kiesSector = sectoren.map((sector, index) => {
        return (
          <option key={index} value={index} selected={answers[stage] === index}>
            {sector}
          </option>
        );
      });
      content = (
        <div>
          <p>In welke sector ben je werkzaam?</p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              <select onChange={changed} name={stage}>
                <option value="-1">Kies een sector...</option>
                {kiesSector}
              </select>
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 3:
      radios = leeftijden.map((leeftijd, index) => {
        return (
          <Form.Check
            type="radio"
            label={leeftijd}
            name={stage}
            value={index}
            checked={answers[stage] === index}
            id={index + 1}
          />
        );
      });
      content = (
        <div>
          <p>Tot welke leeftijdsgroep behoor je?</p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>{radios}</Col>
          </Form.Group>
        </div>
      );
      break;
    case 4:
      radios = [
        "Heel tevreden. Ik kan me tot nu toe vinden in alle genomen maatregelen.",
        "Redelijk tevreden. Ik denk dat het kabinet goed bezig is, maar ik heb wel kritiek op een aantal punten.",
        "Niet tevreden. De tot nu toe genomen maatregelen zijn niet goed overdacht, en kunnen uiteindelijk ingrijpende gevolgen gaan hebben.",
        "Ik weet het niet, ik vind het lastig hier een uitspraak over te doen.",
        "Geen van de bovengenoemde opties.",
      ];
      content = (
        <div>
          <p>
            In hoeverre ben je tevreden met de maatregelen die door het kabinet
            zijn genomen om het virus in de kiem te smoren?
          </p>
          <Form.Group as={Row} onChange={changed}>
            <Col sm={10}>
              {radios.map((radio, index) => {
                return (
                  <Form.Check
                    type="radio"
                    label={radio}
                    name={stage}
                    value={index}
                    checked={answers[stage] === index}
                    id={index + 1}
                    key={index}
                  />
                );
              })}
            </Col>
          </Form.Group>
        </div>
      );
      break;
    case 5:
      content = <p></p>;
      break;
    case 6:
      content = <p></p>;
      break;
    case 7:
      content = <p></p>;
      break;
    case 8:
      title = <h1>Bijna Klaar!</h1>;
      content = (
        <div>
          <p>
            Bedankt voor het invullen. Vul hier je emailadres in. Hier wordt een
            versleutelde versie van opgeslagen in de databank.
          </p>
          <input type="email" onChange={changed} value={email} />
        </div>
      );
      buttons = (
        <div>
          <Button onClick={clicked} variant="secondary" name="vorige">
            Vorige
          </Button>
          <Button onClick={clicked} variant="secondary" name="verstuur">
            Verstuur
          </Button>
        </div>
      );
      break;
    case 9:
      title = <h1>Done!</h1>;
      content = <Link to="/stats">Bekijk de statistieken</Link>;
      buttons = null;
      break;
    default:
      content = null;
  }

  return (
    <Container className={classes.container}>
      {title}
      <p className={classes.content}>
        <ProgressBar animated variant="warning" now={(stage / length) * 100} />
        <Alert alertColor={alertColor}>{alertMessage}</Alert>
        {content}
      </p>
      {buttons}
    </Container>
  );
};

export default Content;
